import { selectConfigLogo } from '@containers/Home/selectors';
import { memo, useCallback } from 'react';
import { useSelector } from 'react-redux';

const Logo = memo(() => {
  const configLogo = useSelector(selectConfigLogo);

  const onClickLogo = useCallback(() => {
    (window as any).open(configLogo?.url, '_blank');
  }, [configLogo]);

  return !!configLogo?.isEnabled && !!configLogo?.image ? (
    <div className={`--logo ${configLogo?.position || 'topLeft'}`}>
      <img
        src={configLogo?.image}
        alt="logo"
        onClick={onClickLogo}
        style={{ width: `${configLogo?.size || 180}px` }}
      />
    </div>
  ) : null;
});

export default Logo;
